
.Header{
  display: flex;
  justify-content:space-between;
  align-items:center;
  height: 41px;
  padding: 40px;
  border-bottom: 1px solid #EAEAEA;
  
}
.Header p{
  font-weight: 400;
  color: #9D9D9D;
  font-size: 14px;
  text-decoration: none;

}
.RightHeader{
  display: flex;
  justify-content: space-around;
  width: 200px;
}
.RightHeader p {
  color: #5C5C5C;
  font-size: 14px;

}
.LeftHeader{
  display: flex;
}
.HeaderTitle{
  margin-left: 16px;
  
}
.shoppingCart{
  cursor: pointer;
}
.liked{
  cursor: pointer;
}
